.App { 
  height: 100vh;
  padding-top: 10px ;
  background-color: #1b1b32;
  font-family: Lato, sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.logo-contenedor {
  width: 100%;
  height: 50px;
  margin: 8px;
  display: flex;
  align-items: center;
  background-color: antiquewhite;
}
.contenedor-calculador {
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: #9909ff;
  border-radius: 20px;
  border: 5px solid white;
}

.fila {
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}